import {
    fetchStudents,
    IStudent,
    modifyStudent,
    fetchCourseDetail,
    getCoursesList,
    modifyCourseDetail,
    modifyCourseDetailParams,
    IListItem,
    getServicesList,
    searchStudent
} from "../../services/students";
import { validatePhone } from "../../utils/validate";


