import { useEffect, useState } from "react"
import { fetchAllProductInfo, fetchProductInfo, IItems } from "../../services/course";

interface IData{
    g1Date: IItems[],
    g2Date: IItems[],
    g3Date: IItems[],
    g4Date: IItems[],
    g5Date: IItems[],
    gkDate: IItems[],
    productList: {key:string,value:string}[]
}

export const useAllInfo = () => {
    const [data, setData] = useState<IData>({
        g1Date: [],
        g2Date: [],
        g3Date: [],
        g4Date: [],
        g5Date: [],
        gkDate: [],
        productList:[]
    });
    useEffect(() => {
        // return;
        fetchAllProductInfo().then((v) => {
            const productList = v.course_ids.map(v => ({ key: v.id, value: v.course_overview__display_name }));
            setData({
                g1Date: v.g1_items,
                g2Date: v.g2_items,
                g3Date: v.g3_items,
                g4Date: v.g4_items,
                g5Date: v.g5_items,
                gkDate: v.gk_items,
                productList
            })
        })
    }, []);

    return { data };
}

export const useProductInfo = (product: string, disabled:boolean) => {
    const [loading,setLoading] = useState(false);
    const [checkedData, setCheckedData] = useState<number[]>([]);
    useEffect(() => {
        if (disabled&&product) {
            setLoading(true);
            fetchProductInfo(product).then((v) => {
                setCheckedData(v.added_items);
            }).finally(() => {
                setLoading(false)
            })
        }
    }, [product,disabled]);

    return {
        loading,
        checkedData,
        setCheckedData
    };
}

